'use client';

import { useState, useEffect } from 'react';
import CalendarShowcase from '@/components/CalendarShowcase';

const API_URL = process.env.NEXT_PUBLIC_API_URL || 'http://localhost:5000/api';

interface Appointment {
  id: number;
  serviceId: number;
  date: string;
  time: string;
  customerName: string;
  customerEmail: string;
  customerPhone: string;
  notes: string;
  status: 'pending' | 'approved' | 'completed' | 'cancelled';
  service?: {
    id: number;
    name: string;
    price: number;
    duration: number;
  };
}

export default function CalendarDemo() {
  const [appointments, setAppointments] = useState<Appointment[]>([]);

  useEffect(() => {
    fetchAppointments();
  }, []);

  const fetchAppointments = async () => {
    try {
      const response = await fetch(`${API_URL}/admin/appointments`);
      const data = await response.json();
      setAppointments(data);
    } catch (error) {
      console.error('Error fetching appointments:', error);
      // Fallback demo data
      setAppointments([
        {
          id: 1,
          serviceId: 1,
          date: '2025-11-08',
          time: '10:00',
          customerName: 'Ana Anić',
          customerEmail: 'ana@example.com',
          customerPhone: '+385 91 123 4567',
          notes: 'Francuski manikir',
          status: 'approved',
          service: { id: 1, name: 'Francuski manikir', price: 15, duration: 60 }
        },
        {
          id: 2,
          serviceId: 2,
          date: '2025-11-08',
          time: '14:00',
          customerName: 'Marija Marić',
          customerEmail: 'marija@example.com',
          customerPhone: '+385 91 234 5678',
          notes: 'Gel lak',
          status: 'pending',
          service: { id: 2, name: 'Gel lak', price: 20, duration: 90 }
        },
        {
          id: 3,
          serviceId: 3,
          date: '2025-11-10',
          time: '11:00',
          customerName: 'Ivana Ivanić',
          customerEmail: 'ivana@example.com',
          customerPhone: '+385 91 345 6789',
          notes: 'Nail art',
          status: 'completed',
          service: { id: 3, name: 'Nail art', price: 25, duration: 120 }
        },
        {
          id: 4,
          serviceId: 1,
          date: '2025-11-12',
          time: '09:00',
          customerName: 'Petra Petrović',
          customerEmail: 'petra@example.com',
          customerPhone: '+385 91 456 7890',
          notes: 'Francuski manikir',
          status: 'cancelled',
          service: { id: 1, name: 'Francuski manikir', price: 15, duration: 60 }
        },
        {
          id: 5,
          serviceId: 4,
          date: '2025-11-15',
          time: '16:00',
          customerName: 'Katarina Katić',
          customerEmail: 'katarina@example.com',
          customerPhone: '+385 91 567 8901',
          notes: 'Akrylne nokte',
          status: 'approved',
          service: { id: 4, name: 'Akrylne nokte', price: 35, duration: 150 }
        }
      ]);
    }
  };

  return (
    <div className="min-h-screen bg-gray-50 dark:bg-gray-900 py-12">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="text-center mb-12">
          <h1 className="text-4xl md:text-5xl font-bold text-gray-900 dark:text-white mb-4">
            Dizajni kalendara za admin panel
          </h1>
          <p className="text-xl text-gray-600 dark:text-gray-300 max-w-3xl mx-auto">
            Odaberite dizajn kalendara koji vam se najviše sviđa za vaš admin dashboard.
            Svaki dizajn prikazuje termine na drugačiji način.
          </p>
        </div>

        <CalendarShowcase appointments={appointments} />

        <div className="mt-12 text-center">
          <div className="bg-blue-50 dark:bg-blue-900/20 border border-blue-200 dark:border-blue-800 rounded-lg p-6 max-w-2xl mx-auto">
            <h3 className="text-lg font-semibold text-blue-900 dark:text-blue-100 mb-2">
              Kako implementirati odabrani dizajn?
            </h3>
            <p className="text-blue-800 dark:text-blue-200">
              Jednostavno mi recite koji dizajn vam se sviđa i ja ću ga implementirati u vaš admin dashboard.
              Dizajn će biti integriran sa postojećom funkcionalnošću za upravljanje terminima.
            </p>
          </div>
        </div>
      </div>
    </div>
  );
}