'use client';

import { useState } from 'react';
import Calendar from 'react-calendar';
import 'react-calendar/dist/Calendar.css';
import { format, isSameDay } from 'date-fns';

interface Appointment {
  id: number;
  serviceId: number;
  date: string;
  time: string;
  customerName: string;
  customerEmail: string;
  customerPhone: string;
  notes: string;
  status: 'pending' | 'approved' | 'completed' | 'cancelled';
  service?: {
    id: number;
    name: string;
    price: number;
    duration: number;
  };
}

interface CalendarDesign {
  id: string;
  name: string;
  description: string;
  tileContent: (props: { date: Date; view: string }) => JSX.Element | null;
  tileClassName?: (props: { date: Date; view: string }) => string | null;
}

interface CalendarShowcaseProps {
  appointments: Appointment[];
}

export default function CalendarShowcase({ appointments }: CalendarShowcaseProps) {
  const [selectedDesign, setSelectedDesign] = useState<string>('classic');

  const getAppointmentsForDate = (date: Date) => {
    return appointments.filter(apt => {
      const aptDate = new Date(apt.date);
      return isSameDay(aptDate, date);
    });
  };

  const getStatusCounts = (date: Date) => {
    const dayAppointments = getAppointmentsForDate(date);
    return {
      pending: dayAppointments.filter(a => a.status === 'pending').length,
      approved: dayAppointments.filter(a => a.status === 'approved').length,
      completed: dayAppointments.filter(a => a.status === 'completed').length,
      cancelled: dayAppointments.filter(a => a.status === 'cancelled').length,
      total: dayAppointments.length
    };
  };

  const calendarDesigns: CalendarDesign[] = [
    {
      id: 'classic',
      name: 'Klasični dizajn',
      description: 'Jednostavan dizajn sa pink točkicama za dane sa terminima',
      tileContent: ({ date, view }) => {
        if (view === 'month') {
          const dayAppointments = getAppointmentsForDate(date);
          if (dayAppointments.length > 0) {
            return (
              <div className="flex justify-center mt-1">
                <div className="w-2 h-2 bg-pink-500 rounded-full"></div>
              </div>
            );
          }
        }
        return null;
      }
    },
    {
      id: 'numbered',
      name: 'Brojevni dizajn',
      description: 'Prikazuje broj termina u svakom danu',
      tileContent: ({ date, view }) => {
        if (view === 'month') {
          const count = getAppointmentsForDate(date).length;
          if (count > 0) {
            return (
              <div className="flex justify-center mt-1">
                <div className="w-5 h-5 bg-blue-500 text-white text-xs rounded-full flex items-center justify-center font-bold">
                  {count}
                </div>
              </div>
            );
          }
        }
        return null;
      }
    },
    {
      id: 'status-colors',
      name: 'Boje po statusu',
      description: 'Različite boje za različite statuse termina',
      tileContent: ({ date, view }) => {
        if (view === 'month') {
          const counts = getStatusCounts(date);
          if (counts.total > 0) {
            return (
              <div className="flex justify-center mt-1 space-x-1">
                {counts.pending > 0 && <div className="w-1.5 h-1.5 bg-yellow-400 rounded-full"></div>}
                {counts.approved > 0 && <div className="w-1.5 h-1.5 bg-blue-500 rounded-full"></div>}
                {counts.completed > 0 && <div className="w-1.5 h-1.5 bg-green-500 rounded-full"></div>}
                {counts.cancelled > 0 && <div className="w-1.5 h-1.5 bg-red-500 rounded-full"></div>}
              </div>
            );
          }
        }
        return null;
      }
    },
    {
      id: 'gradient',
      name: 'Gradijent dizajn',
      description: 'Moderan dizajn sa gradijentima i brojevima',
      tileContent: ({ date, view }) => {
        if (view === 'month') {
          const count = getAppointmentsForDate(date).length;
          if (count > 0) {
            return (
              <div className="flex justify-center mt-1">
                <div className="w-6 h-6 bg-gradient-to-r from-pink-400 to-purple-500 text-white text-xs rounded-full flex items-center justify-center font-bold shadow-sm">
                  {count}
                </div>
              </div>
            );
          }
        }
        return null;
      },
      tileClassName: ({ date, view }) => {
        if (view === 'month') {
          const count = getAppointmentsForDate(date).length;
          if (count > 0) {
            return 'calendar-gradient-day';
          }
        }
        return null;
      }
    },
    {
      id: 'minimal',
      name: 'Minimalistički dizajn',
      description: 'Čist dizajn sa suptilnim indikatorima',
      tileContent: ({ date, view }) => {
        if (view === 'month') {
          const count = getAppointmentsForDate(date).length;
          if (count > 0) {
            return (
              <div className="flex justify-center mt-1">
                <div className="w-3 h-0.5 bg-gray-600 rounded-full opacity-60"></div>
              </div>
            );
          }
        }
        return null;
      }
    },
    {
      id: 'detailed',
      name: 'Detaljni dizajn',
      description: 'Prikazuje broj termina i status indikatore',
      tileContent: ({ date, view }) => {
        if (view === 'month') {
          const counts = getStatusCounts(date);
          if (counts.total > 0) {
            return (
              <div className="flex flex-col items-center mt-1 space-y-0.5">
                <div className="text-xs font-bold text-gray-700">{counts.total}</div>
                <div className="flex space-x-0.5">
                  {counts.pending > 0 && <div className="w-1 h-1 bg-yellow-400 rounded-full"></div>}
                  {counts.approved > 0 && <div className="w-1 h-1 bg-blue-400 rounded-full"></div>}
                  {counts.completed > 0 && <div className="w-1 h-1 bg-green-400 rounded-full"></div>}
                </div>
              </div>
            );
          }
        }
        return null;
      }
    },
    {
      id: 'card-style',
      name: 'Card stil',
      description: 'Kalendar sa card-like dizajnom dana',
      tileContent: ({ date, view }) => {
        if (view === 'month') {
          const counts = getStatusCounts(date);
          if (counts.total > 0) {
            return (
              <div className="absolute inset-0 bg-gradient-to-br from-pink-50 to-purple-50 dark:from-pink-900/20 dark:to-purple-900/20 rounded-lg border border-pink-200 dark:border-pink-800">
                <div className="flex flex-col items-center justify-center h-full text-xs">
                  <div className="font-bold text-pink-600 dark:text-pink-400">{counts.total}</div>
                  <div className="flex space-x-0.5 mt-0.5">
                    {counts.pending > 0 && <div className="w-1 h-1 bg-yellow-400 rounded-full"></div>}
                    {counts.approved > 0 && <div className="w-1 h-1 bg-blue-400 rounded-full"></div>}
                    {counts.completed > 0 && <div className="w-1 h-1 bg-green-400 rounded-full"></div>}
                  </div>
                </div>
              </div>
            );
          }
        }
        return null;
      },
      tileClassName: ({ date, view }) => {
        if (view === 'month') {
          const count = getAppointmentsForDate(date).length;
          if (count > 0) {
            return 'calendar-card-day';
          }
        }
        return null;
      }
    },
    {
      id: 'neon',
      name: 'Neon dizajn',
      description: 'Svijetleći neon efekti za moderne salone',
      tileContent: ({ date, view }) => {
        if (view === 'month') {
          const count = getAppointmentsForDate(date).length;
          if (count > 0) {
            return (
              <div className="flex justify-center mt-1">
                <div className="w-6 h-6 bg-pink-400 text-white text-xs rounded-full flex items-center justify-center font-bold shadow-lg shadow-pink-400/50 border-2 border-pink-300">
                  {count}
                </div>
              </div>
            );
          }
        }
        return null;
      }
    }
  ];

  const selectedDesignData = calendarDesigns.find(d => d.id === selectedDesign) || calendarDesigns[0];

  return (
    <div className="bg-white dark:bg-gray-800 rounded-lg shadow p-6">
      <h2 className="text-2xl font-semibold text-gray-900 dark:text-white mb-6">
        Dizajni kalendara - Odaberite svoj omiljeni
      </h2>

      {/* Design Selection */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4 mb-8">
        {calendarDesigns.map((design) => (
          <button
            key={design.id}
            onClick={() => setSelectedDesign(design.id)}
            className={`p-4 rounded-lg border-2 transition-all ${
              selectedDesign === design.id
                ? 'border-pink-500 bg-pink-50 dark:bg-pink-900/20'
                : 'border-gray-200 dark:border-gray-600 hover:border-pink-300'
            }`}
          >
            <h3 className="font-semibold text-gray-900 dark:text-white mb-2">
              {design.name}
            </h3>
            <p className="text-sm text-gray-600 dark:text-gray-400">
              {design.description}
            </p>
          </button>
        ))}
      </div>

      {/* Calendar Preview */}
      <div className="mb-6">
        <h3 className="text-xl font-semibold text-gray-900 dark:text-white mb-4">
          Pregled: {selectedDesignData.name}
        </h3>
        <div className="flex justify-center">
          <div className="calendar-container">
            <Calendar
              value={new Date()}
              tileContent={selectedDesignData.tileContent}
              tileClassName={selectedDesignData.tileClassName}
              className="border rounded-lg shadow-lg"
            />
          </div>
        </div>
      </div>

      {/* Legend */}
      <div className="bg-gray-50 dark:bg-gray-700 rounded-lg p-4">
        <h4 className="font-semibold text-gray-900 dark:text-white mb-3">Legenda:</h4>
        <div className="grid grid-cols-2 md:grid-cols-4 gap-4 text-sm">
          <div className="flex items-center space-x-2">
            <div className="w-3 h-3 bg-yellow-400 rounded-full"></div>
            <span className="text-gray-700 dark:text-gray-300">Na čekanju</span>
          </div>
          <div className="flex items-center space-x-2">
            <div className="w-3 h-3 bg-blue-500 rounded-full"></div>
            <span className="text-gray-700 dark:text-gray-300">Potvrđen</span>
          </div>
          <div className="flex items-center space-x-2">
            <div className="w-3 h-3 bg-green-500 rounded-full"></div>
            <span className="text-gray-700 dark:text-gray-300">Završen</span>
          </div>
          <div className="flex items-center space-x-2">
            <div className="w-3 h-3 bg-red-500 rounded-full"></div>
            <span className="text-gray-700 dark:text-gray-300">Otkazan</span>
          </div>
        </div>
      </div>

      <style jsx global>{`
        .calendar-container .react-calendar {
          width: 100%;
          max-width: 100%;
          background: white;
          border: 1px solid rgb(229 231 235);
          font-family: inherit;
          border-radius: 0.5rem;
          padding: 1rem;
        }
        .dark .calendar-container .react-calendar {
          background: rgb(31 41 55);
          border: 1px solid rgb(75 85 99);
          color: rgb(243 244 246);
        }
        .calendar-container .react-calendar__navigation {
          background: transparent;
        }
        .dark .calendar-container .react-calendar__navigation button {
          color: rgb(243 244 246);
        }
        .calendar-container .react-calendar__navigation button:enabled:hover,
        .calendar-container .react-calendar__navigation button:enabled:focus {
          background-color: rgb(243 244 246);
        }
        .dark .calendar-container .react-calendar__navigation button:enabled:hover,
        .dark .calendar-container .react-calendar__navigation button:enabled:focus {
          background-color: rgb(55 65 81);
        }
        .calendar-container .react-calendar__month-view__weekdays__weekday {
          color: rgb(107 114 128);
        }
        .dark .calendar-container .react-calendar__month-view__weekdays__weekday {
          color: rgb(156 163 175);
        }
        .calendar-container .react-calendar__tile {
          padding: 0.75em 0.5em;
          position: relative;
          background: white;
          color: rgb(17 24 39);
        }
        .dark .calendar-container .react-calendar__tile {
          background: rgb(31 41 55);
          color: rgb(243 244 246);
        }
        .calendar-container .react-calendar__tile:enabled:hover,
        .calendar-container .react-calendar__tile:enabled:focus {
          background: rgb(243 244 246);
        }
        .dark .calendar-container .react-calendar__tile:enabled:hover,
        .dark .calendar-container .react-calendar__tile:enabled:focus {
          background: rgb(55 65 81);
        }
        .calendar-container .react-calendar__tile--active {
          background: #ec4899 !important;
          color: white !important;
        }
        .calendar-container .react-calendar__tile--active:enabled:hover,
        .calendar-container .react-calendar__tile--active:enabled:focus {
          background: #db2777 !important;
        }
        .calendar-gradient-day {
          background: linear-gradient(135deg, rgba(236, 72, 153, 0.1), rgba(168, 85, 247, 0.1)) !important;
          border-radius: 8px !important;
        }
        .calendar-card-day {
          position: relative !important;
          overflow: hidden !important;
        }
        .calendar-card-day .react-calendar__tile {
          position: relative !important;
        }
      `}</style>
    </div>
  );
}